import React from 'react';
import { Panel, PanelHeaderSimple, Button, Group, Div } from '@vkontakte/vkui';

import { MODAL_ARE_AT_HOME } from '../modals/Modal';

// Redux
import { connect } from 'react-redux';
import { openModal } from '../redux/actions/modal';

import homePng from '../img/home.png';
import '../styles/header.css';
import './Begin.css';

class Begin extends React.Component {
	render() {
		return (
			<Panel id={this.props.id} separator={false}>
				<PanelHeaderSimple separator={false}><b>#</b><b>Л</b><b>У</b><b>Ч</b><b>Ш</b><b>Е</b><b>Д</b><b>О</b><b>М</b><b>А</b></PanelHeaderSimple>
				<Group>
					<img className="Home" src={homePng} alt="Дом" />
					<Div className="Explanation">
						<p>В сегодняшних реалиях пандемии короновирусной инфекции Covid-19, самым эффективным средством для защиты себя и своих близких ДО СИХ ПОР является САМОИЗОЛЯЦИЯ от источников угроз!</p>
						<p>Присоединяйтесь к сообществу людей, готовых взять на себя ответственность за наше совместное будущее!</p>
					</Div>
					<Div>
						<Button size="xl" level="2" onClick={() => {this.props.onOpenModal(MODAL_ARE_AT_HOME)}}>Начать!</Button>
					</Div>
				</Group>
			</Panel>
		);
}}

const mapStateToProps = (state, ownProps) => {
	return {};
};

const mapDispatchToProps = (dispatch) => {
	return {
		// Открывает модальное окно
		onOpenModal: (activeModal, modalParams) =>
			new Promise((resolve) =>
				openModal(activeModal, modalParams).then((action) => {
					dispatch(action);
					resolve();
				})
			)
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Begin);
