import React from 'react';
import { Panel, List, Cell, Group, ScreenSpinner, Div, FormLayout, File, Button } from '@vkontakte/vkui';

import { MODAL_REWARD } from '../modals/Modal';

import Api from '../core/api/Api';

import {getGroupName} from '../components/DangerMeter';

// Redux
import { connect } from 'react-redux';
import { openModal } from '../redux/actions/modal';

import Icon24Camera from '@vkontakte/icons/dist/24/camera';
import './Dairy.css';

class Dairy extends React.Component {

	constructor(props) {
    super(props);
    this.state = {isLoading: true};
  };

	// Компонент примонтирован
	componentDidMount() {
		Api.rating.global((table) => {
			this.setState({isLoading: false, table: table});
		});

		// Upload
		const input = document.getElementById('upload-input');
		const onSelectFile = () => Api.upload('dairy/upload?uid='+this.props.fetchedUser.id, input);
		input && input.addEventListener('change', onSelectFile, false);
  };

	submit() {
		Api.upload('dairy/upload?uid='+this.props.fetchedUser.id, document.getElementById('upload-form'));
	}

	render() {
		return (
			<Panel id={this.props.id}>
				{this.props.fetchedUser && <FormLayout action={Api.getDomain('dairy/upload?uid='+this.props.fetchedUser.id)} id="upload-form" className="UploadFrom">
	        <File before={<Icon24Camera />} size="l" id="upload-input">
	          Открыть галерею
	        </File>
					<Button onClick={this.submit}>Добавить</Button>
	      </FormLayout>}
				{!this.state.isLoading && <Group>
		      <List className="Dairy"></List>
				</Group>}
				{this.state.isLoading && <ScreenSpinner size='large' />}
			</Panel>
		);
	}
}

const mapStateToProps = (state, ownProps) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
	return {
		// Открывает модальное окно
		onOpenModal: (activeModal, modalParams) =>
			new Promise((resolve) =>
				openModal(activeModal, modalParams).then((action) => {
					dispatch(action);
					resolve();
				})
			)
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Dairy);
