import React from 'react';
import { Panel, List, Cell, Group, ScreenSpinner, Div, Avatar, PanelHeaderSimple, PanelHeaderButton } from '@vkontakte/vkui';
import { platform, IOS } from '@vkontakte/vkui';

import { MODAL_REWARD } from '../modals/Modal';

import Api from '../core/api/Api';

import {getGroupName} from '../components/DangerMeter';

// Redux
import { connect } from 'react-redux';
import { navigate } from '../redux/actions/navigation';
import { openModal } from '../redux/actions/modal';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import './Rating.css';

const osName = platform();

class Rating extends React.Component {

	constructor(props) {
    super(props);
    this.state = {isLoading: true};
  };

	// Компонент примонтирован
	componentDidMount() {
		Api.rating.global((table) => {
			this.setState({isLoading: false, table: table});
		});
  };

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeaderSimple
					left={<PanelHeaderButton onClick={() => {this.props.onNavigate('level')}}>
						{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</PanelHeaderButton>}
				>
					Рейтинг
				</PanelHeaderSimple>
				{!this.state.isLoading && <Group>
		      <List className="RatingList">
						<Cell before={<Avatar />}>
							<Div></Div>
							<Div>Баллы</Div>
						</Cell>
						{this.state.table.map((friend, index) => {
              return (
								<Cell
									key={"friend_" + friend.uid}
									before={friend.photo_200 ? <Avatar src={friend.photo_200}/> : null}
									description={getGroupName(friend.group, true)}
								>
			        		<Div>
										{`${friend.first_name} ${friend.last_name}`}
										{friend.rank && <div className="Rank">{friend.rank}</div>}
									</Div>
									<Div className="Point">
										{friend.value}
									</Div>
								</Cell>
							);
						})}
					</List>
				</Group>}
				{this.state.isLoading && <ScreenSpinner size='large' />}
			</Panel>
		);
	}
}

const mapStateToProps = (state, ownProps) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
	return {
		// Переход на другую панель/представление
		onNavigate: (activeStory, activePanel, params) =>
				new Promise((resolve) =>
					navigate(activeStory, activePanel, params).then((action) => {
						dispatch(action);
						resolve();
					})
				),
		// Открывает модальное окно
		onOpenModal: (activeModal, modalParams) =>
			new Promise((resolve) =>
				openModal(activeModal, modalParams).then((action) => {
					dispatch(action);
					resolve();
				})
			)
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Rating);
