/**
 * Редуктор навигации
 */

export default function reducer(state = null, action) {
  switch (action.type) {
    // Сменить активный экран
    case 'NAVIGATE':
      return Object.assign({}, state, {activeStory: action.activeStory, activePanel: action.activePanel, params: action.params});

    default:
      return state;
    }
}
