/**
 * Действия с навигацией
 */

/**
 * Сменить активный экран
 *
 * @param  {String} activeStory
 * @param  {Object} params
 * @return {Promise}
 */
export const navigate = (activeStory, activePanel, params) => {
  return new Promise((resolve, reject) => {

      if(activeStory.currentTarget && activeStory.currentTarget.dataset && activeStory.currentTarget.dataset.story) {
        activeStory = activeStory.currentTarget.dataset.story;
      }

      if(!activePanel) {
        activePanel = activeStory;
      }

      resolve({
        type: 'NAVIGATE',
        activeStory: activeStory,
        activePanel: activePanel,
        params: params
      });
  });
};
