import React from 'react';
import { ModalCard } from '@vkontakte/vkui';

import {MODAL_NOT_HOME_IN_START} from './Modal';

import Api from '../core/api/Api';

import Icon28BlockOutline from '@vkontakte/icons/dist/28/block_outline';

export default (modalClose, modalParams) => {
  return (<ModalCard
    id={MODAL_NOT_HOME_IN_START}
    onClose={modalClose}
    icon={<Icon28BlockOutline />}
    header="Судя по данным, вы покинули дом"
    caption="Если вы сейчас дома, значит, в начале был определен не верный адрес. Пока его ещё можно поментять. Нажмите «Здесь дом», чтобы поменя домашний адрес на текущий"
    actions={[
      {
        title: 'Здесь дом',
        mode: 'secondary',
        action: () => {
          Api.home.update(modalParams.uid, modalParams.lat, modalParams.long, (level) => modalParams.onLevelUpdate(level).then(modalClose));
        }
      },{
        title: 'Я не дома',
        mode: 'secondary',
        action: modalClose
      }
    ]}
  >
  </ModalCard>);
};
