/**
 * Работа с сервером
 */
import home from './home';
import rating from './rating';

const config = {
  domain: //window.location.host !== 'orlove.github.io' ? 'https://api.newsapp.me' :
                                                       'https://rest-news.hozya.ru'
};

var api = {
  /**
   * Запрос данных с сервера
   * @param  {String}   method  api-метод
   * @param  {Object}   params  параметры api-метода
   * @param  {mix}      tag     ключ запроса (для возможности его отмены)
   * @param  {Function} handler обработчик ответа в случае успеха
   * @param  {Object}   options доп. опции http-запроса (заголовки, метод и т.д.)
   */
  request: function ({method, params, tag, handler, options}) {

    // Отменяем предыдущий ещё не завершенный запрос
    if(tag) {
      fetch.abort(tag);
    }

    // Преобразуем в url
    if(params) {
      params = Object.keys(params).map((k) =>
          encodeURIComponent(k) + '=' + encodeURIComponent(params[k])
      ).join('&');
      params = '?'+params;
    } else {
      params = '';
    }

    return fetch(config.domain+'/'+method+params, options, tag)
      .then((response) => {
        if(response.ok) {
          return response.json();
        } else {
          response.text().then((text) => {
            console.error(response.url, text.substr(-64), response);
          });
          return false;
        }
      })
      .then(handler)
      .catch((error) => {
        console.error(error);
        //this.setState({error: error, isLoading: false});
      });
  },
  /**
   * POST-запросы
   * @param  {String}   method  api-метод
   * @param  {Object}   body    тело запроса
   * @param  {mix}      tag     ключ запроса (для возможности его отмены)
   * @param  {Function} handler обработчик ответа в случае успеха
   * @param  {Object}   options доп. опции http-запроса (заголовки, метод и т.д.)
   */
  post: function ({method, body, tag, handler, options}) {

    // Отменяем предыдущий ещё не завершенный запрос
    if(tag) {
      fetch.abort(tag);
    }

    options = Object.assign({
      method: 'POST',
      /*headers: {
        'Content-Type': 'application/json;charset=utf-8'
      },*/
      body: JSON.stringify(body)
    }, options || {});

    return fetch(config.domain+'/'+method, options, tag)
      .then((response) => {
        if(response.ok) {
          return response.json();
        } else {
          response.text().then((text) => {
            console.error(response.url, text.substr(-64), response);
          });
          return false;
        }
      })
      .then(handler)
      .catch((error) => {
        console.error(error);
        //this.setState({error: error, isLoading: false});
      });
  },
  /**
   * Отменяет запрос с ключем tag
   * @param {mix} tag ключ запроса
   */
  abort: (tag) => {
    fetch.abort(tag);
  },
  /**
   * Возвращает домен
   * @return {[type]} [description]
   */
  getDomain: (route) => {
    if(route) {
      return config.domain+'/'+route;
    } else {
      return config.domain;
    }
  },
  /**
   * Отправить форму (без файла)
   * @type {[type]}
   */
  submit: (from) => {
    const data = new URLSearchParams(new FormData(from));

    return fetch(from.action, {
        method: 'post',
        body: data,
    });
  },
  /**
   * Отправить форму (без файла)
   * @type {[type]}
   */
  upload: (route, fileInput) => {
    console.log(fileInput);

    const formData = new FormData();

    formData.append('file', fileInput.files[0]);

    const options = {
      method: 'POST',
      body: formData
    };

    return fetch(config.domain+'/'+route, options);
  }
};

// Сливаем каждый компонент с api
[home, rating].forEach((a) => {
  api = Object.assign(a, api);
});

export default api;
