import React from 'react';
import { ModalCard } from '@vkontakte/vkui';

import {MODAL_REWARD} from './Modal';

import tips from '../constants/tips';

import './Reward.css';

import Icon44SmileOutline from '@vkontakte/icons/dist/44/smile_outline';
import Icon24Info from '@vkontakte/icons/dist/24/info';

export default (modalClose, modalParams) => {
  return (<ModalCard
    id={MODAL_REWARD}
    onClose={modalClose}
    icon={<Icon44SmileOutline />}
    header="Так держать!"
    caption={<div>
      <p>Отметка подтверждена, вам начислено {modalParams.value} баллов.</p>
      <div className="Tip"><Icon24Info /><p>{tips[Math.round(Math.random()*tips.length)]}</p></div>
    </div>}
  >
  </ModalCard>);
};
