/**
 * Компонент навигации
 * @type {Object}
 */

import React from 'react';
import { Epic, Tabbar, TabbarItem, View, Panel } from '@vkontakte/vkui';

import Begin from '../panels/Begin';
//import Map from '../panels/Map';
import Level from '../panels/Level';
import Rating from '../panels/Rating';
import Dairy from '../panels/Dairy';

import Modal from '../modals/Modal';

// Redux
import { connect } from 'react-redux';
import { navigate } from '../redux/actions/navigation';

import Icon28NameTagOutline from '@vkontakte/icons/dist/28/name_tag_outline';
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline';
import Icon28GraphOutline from '@vkontakte/icons/dist/28/graph_outline';

class EpicBar extends React.Component {
  constructor (props) {
    super(props);
  }

  render () {

    return (
      <Epic activeStory={this.props.activeStory} tabbar={false/*
        this.props.activePanel !== 'begin' && <Tabbar>
          <TabbarItem
            onClick={this.props.onNavigate}
            selected={this.props.activeStory === 'level'}
            data-story="level"
            text="Прогрес"
          ><Icon28NameTagOutline /></TabbarItem>
          <TabbarItem
            onClick={this.props.onNavigate}
            selected={this.props.activeStory === 'dairy'}
            data-story="dairy"
            text="Дневник"
          ><Icon28NewsfeedOutline /></TabbarItem>
          <TabbarItem
            onClick={this.props.onNavigate}
            selected={this.props.activeStory === 'rating'}
            data-story="rating"
            text="Рейтинг"
          ><Icon28GraphOutline /></TabbarItem>
        </Tabbar>
      */}>
        <View id="level" activePanel={this.props.activePanel} header={false} modal={<Modal permission={this.props.permission} />}>
          <Level id='level' fetchedUser={this.props.user} permission={this.props.permission} />
          <Begin id='begin' />
          {/*<Map id='map' />*/}
        </View>
        <View id="dairy" activePanel={this.props.activePanel} header={false} modal={<Modal permission={this.props.permission} />}>
          <Dairy id='dairy' fetchedUser={this.props.user} />
        </View>
        <View id="rating" activePanel={this.props.activePanel} header={false} modal={<Modal permission={this.props.permission} />}>
          <Rating id='rating' />
        </View>
      </Epic>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  return state.navigation ? {
    activeStory: state.navigation.activeStory,
    activePanel: state.navigation.activePanel,
    params: state.navigation.params
  } : {};
};

const mapDispatchToProps = (dispatch) => {
	return {
		onNavigate: (activeStory, activePanel, params) =>
				new Promise((resolve) =>
					navigate(activeStory, activePanel, params).then((action) => {
						dispatch(action);
						resolve();
					})
				)
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(EpicBar);
